.home {
    width: 100vw;
    height: 100vh;
    background-image: url('https://images.unsplash.com/photo-1562329265-95a6d7a83440?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1370&q=80');
    background-size: 100vw 100vh;
}

.home article {
    background-color: white;
    position: absolute;
    height: 200px;
    width: 500px;
    margin-left: -250px;
    margin-top: -100px;
    padding: 20px;
    left: 50%;
    top: 50%;
}

.home > p {
    position: absolute;
    right: 10px;
    bottom: -9px;
    color: white;
    font-size: 0.8em;
}